@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
body {
  font-family: "Nunito", sans-serif;
  /* font-family: "M PLUS Rounded 1c", sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #333333;
  color: #fff;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  /* font-family: "M PLUS Rounded 1c", sans-serif; */
}

.container {
  width: 80%;
  padding: 0.5rem 1rem;
  background: #444;
}

@media (max-width: 500px) {

  .container {
    width: 95%;
  }
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}
.app__header {
}

.app__info {
  margin-bottom: 0.8rem;
}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 800px) {
  .form {
    flex-direction: column;
    height: 13rem;
    align-items: flex-start;
  }
}

.form__label {
  display: flex;
  flex-direction: column;
}

@media (max-width: 800px) {
  .form__label {
    width: 100%;
  }
}

.form__input {
  width: 15rem;
  padding: 0.25rem 0.2rem;
  border-radius: 0.5rem;
  outline: none;
  border: 2px solid #fff;
  margin-top: 0.6rem;
}

.form__input:focus {
  border: 2px solid #ffa000;
}

@media (max-width: 800px) {
  .form__input {
    width: 100%;
  }
}

.form__button {
  background: #ffa000;
  color: #fff;
  border: 2px solid #ffa000;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.5rem 1rem;
  border-radius: 99px;
  font-weight: 700;
  transition: all 0.2s;
}

.form__button:hover {
  background: transparent;
}
/*# sourceMappingURL=Form.css.map */
.todo {
  margin-top: 0.8rem;
  padding: 0.5rem 0 0 0;
}

.todo__btnContainer {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 0.5rem;
}

.todo__btn {
  color: #fff;
  background: transparent;
  border: 2px solid #ffa000;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.5rem 1rem;
  border-radius: 0.6rem;
  opacity: 0.7;
  transition: all 0.2s;
}

.todo__btn:hover {
  background: #ffa000;
}

.todo__btn + .todo__btn {
  margin-left: 1rem;
}

.todo__btn.selected {
  background: #ffa000;
  opacity: 1;
}

.todo__noItems {
  padding: 1rem;
}

.todo__noItems > img {
  height: 8rem;
}
/*# sourceMappingURL=Todo.css.map */
.todoItem {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  border-top: 2px solid #333333;
}

.todoItem__info {
  width: 100%;
  transition: all 0.1s;
}

.todoItem__info > h1 {
  position: relative;
  color: #ffa000;
}

.todoItem__info > h1 > span {
  position: absolute;
  right: 0;
  top: .5rem;
  color: #fff;
  font-size: .7rem;
  font-weight: 400;
  opacity: 0.7;
}

.todoItem__info > p {
  max-height: 66px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.todoItem__info.completed {
  opacity: 0.7;
}

.todoItem__info.completed > p {
  text-decoration: line-through;
}

.todoItem__btnContainer {
  display: flex;
  align-items: center;
  padding-left: 0.5rem;
}

.todoItem__btnContainer > button {
  background: #f5f6f7;
  padding: 0.4rem 1rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.2s;
}

.todoItem__btnContainer > .btn__complete {
  border: 2px solid #00aa69;
  color: #00aa69;
  margin-right: 1rem;
}

.todoItem__btnContainer > .btn__complete:hover {
  background: #00aa69;
  color: #fff;
}

.todoItem__btnContainer > .btn__delete {
  border: 2px solid #ca0000;
  color: #ca0000;
}

.todoItem__btnContainer > .btn__delete:hover {
  background: #ca0000;
  color: #fff;
}
/*# sourceMappingURL=TodoItem.css.map */
